<template>
    <div class="all-box clearfix">
        <div class="menufiexd">
            <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
                <van-swipe-item v-for="(item,index) in bannerData" :key="index" >
                    <img class="auto-img" :src="item.bannerImg" alt="">
                </van-swipe-item>
            </van-swipe>
            <div class="title-box-top" >
                <div :class='{active:item.isActive}' class="item fl" v-for='(item,index) in typeData' :key="index" @click='toggletype(item)' >{{item.typeDesc}}</div>
                
            </div>
        </div>

        <div class="title-box-bottom">
            <div class="bottom-mk clearfix" v-for='(item,index) in proData' :key="index" @click="toggledetail(item.pid)">
                <div class="fl">
                    <img class="auto-img" :src="item.smallImg" alt="">
                </div>
                <div class="fr ">
                    <div class="pro-name">{{item.name}}</div>
                    <div class="pro-eng">{{item.enname}}</div>
                    <div class="pro-price">￥{{item.price}}</div>
                </div>
                
            </div>
            
        </div>

    </div>
</template>

<script>
import '../../assets/css/menu.less'
    export default {
        data(){
            return{
                bannerData:[],
                typeData:[],
                proData:[]
            }
        },
        created(){
            this.tobanner();
            this.tomenutit();
            this.topro({type:"isHot"})
        },
        methods:{
            toggledetail(pid){
                this.$router.push({name:'Detail',query:{pid}});
                
            },
            //获取轮播图数据
            tobanner(){

                this.axios({
                    url:"/banner",
                    method:"GET",
                    params:{
                        appkey:this.appkey
                    }
                }).then(result => {
                    this.$toast.clear();
                    
                    if(result.data.code==300){
                        this.bannerData = result.data.result;
                        
                    }
                }).catch(err => {
                    this.$toast.clear();
                    
                })
                
            },
            //获取商品type数据
            tomenutit(){
                this.$toast.loading({
                    message: "加载中...",
                    forbidClick: true,

                    //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
                    duration: 0
                });
                this.axios({
                    url:"/type",
                    method:"GET",
                    params:{
                        appkey:this.appkey
                    }
                }).then(result => {
                    this.$toast.clear();
                    
                    if(result.data.code==400){
                        
                         result.data.result.forEach(v=>{
                            v.isActive = false ;
                        });
                         result.data.result.unshift({type:'isHot',typeDesc:'店长推荐',isActive:true})
                        
                        this.typeData = result.data.result;

                    }
                }).catch(err => {
                    this.$toast.clear();
                    
                })
            },
            //type切换
            toggletype(item){
               if (item.isActive) {
                    return;
                }
                for(let i = 0 ; i < this.typeData.length ; i++){
                    if(this.typeData[i].isActive){
                        this.typeData[i].isActive = false ;
                        break;
                    }
                }
                item.isActive = true;
                this.topro(item);
            },
            topro(item){
                this.proData=[];
                
                let params={
                        appkey:this.appkey,
                        key:"type",
                        value:item.type
                    }
                if(item.type == "isHot"){
                    params.key = 'isHot';
                    params.value = 1;
                };
                this.axios({
                    url:"/typeProducts",
                    method:"GET",
                    
                    params
                }).then(result => {
                    this.$toast.clear();
                    
                    if(result.data.code==500){
                        this.proData=result.data.result;
                    }
                }).catch(err => {
                    this.$toast.clear();
                    
                })
            }
            
        }
    }
</script>

<style lang="less" scoped>




</style>